// Import materialize variables
@import 'components/color';
@import 'components/variables';

// Custom color settings go here
$primary-color: color("teal", "lighten-2");
$grey-lighten: color("grey", "lighten-4");
$primary-color-light: lighten($primary-color, 40%);

// Import materialize components
@import 'materialize';

// Importing Typefaces
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
@import url('http://fonts.googleapis.com/css?family=Open+Sans');

// Import user styles
@import 'map';
@import 'search-component';
@import 'loader';


/* FIXME: Workaround for https://github.com/Dogfalo/materialize/issues/1079 */
@each $mdi-icon-name, $mdi-icon-value in $mdi-list-icons {
  .#{$mdi-prefix}#{$mdi-icon-name}:before {
    content: "\""+ $mdi-icon-value +"\"";
  }
}

$teal: color("teal", "");
$teal-lighten-4: color("teal", "lighten-4");

html, body{
  color: #333;
}

.parallax-container {
  height: 500px;
}

.non-parallax-container{
  background: #FAFAFA;
}

.open-sans{
  font-family: "Open Sans";
}

.no-padding{
  margin: 0 !important;
  padding: 0 !important;
}

.materialize-tabs{
  @extend .no-padding;
}

// Logo
a.website-title{
  font-size: 1.5em;
  @extend .open-sans;
}

nav.website-nav{

  i.material-icons{
    line-height: auto;
  }

  // White Background
  @extend .white;
  height: auto;

  // Navigation Links
  ul.website-nav li a{
    text-transform: uppercase;
    @extend .grey-text;
    @extend .text-darken-3;
    font-size: 0.8em;
  }

}

ul.tabs{

  border-bottom: 1px solid lightgrey;

  .indicator{
    @extend .teal;
  }

  li.tab a{
    @extend .grey-text;
    font-weight: 500;
  }

  li.tab a.active{
    @extend .teal-text;
  }

  li.tab:hover{

    background: none;

    a{
      @include transition(.3s);
      @extend .text-lighten-1;
    }

  }
}

.materialize-tabs{
  @extend .no-padding;
  .col{
    @extend .no-padding;
  }
  .tabs .tab{
    letter-spacing: 0.5px;
  }
}

.card{

  .card-title{
    @extend .teal-text;
  }

}
