.search-panel{
  position: absolute;
  width: 300px;
  top: 80px;
  left: 25px;

  .search-controls{

    a{
      font-size: 0.8em;
      text-transform: none;
    }

  }

}

.no-search-result{
  height: 500px;
  margin-top: 20px;

  .valign{
    width: 100%;
  }

  h4{
    line-height: 1.5em;
  }

}


.map {
  .directory-search {
    padding-top: 0px;
  }

  .search-result {
    margin-top: 25px;
  }

  .directory-table {
    height : 300px;

    tr{
      cursor: pointer;
    }

    td.name {
      width: 30%;
    }

    td.location {
      width: 20%
    }

    td.country {
      width: 20%;
    }

    td.species {
      width: 30%;
    }

    td div{
      display: block;
      display: -webkit-box;
      max-width: 180px;
      height: 3em;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }

  }

  .pagination li {
    margin-left: 5px;
    cursor: pointer;
  }

  .pagination li.active{
    @extend .teal;
  }

}

